<template>
  <div class='facerecognize'>
    <div class="user-info">
      <div class="user-info-wraper">
        <div class="user-pic-wraper">
          <img class="user-pic" :src="this.$store.state.userPic">
        </div>
        <div class="usermsg-userid">ID{{ userId }}</div>
      </div>
    </div>

    <div class="content">

      <div class="photobox">
        <div id="photo">
          <h3 class="photo-title"></h3>
          <div
            class="container"
          >
            <img v-if="hasImg" src="https://cdn.ai.qq.com/ai/page/product/face/img/banner-ico-7706573879.png">
            <img
              id="Img"
            >
          </div>
        </div>
        <div class="inputwrap">
          <div class="upload">
            
            <input
              type="file"
              id="imageUpload"
              @change="onImgchange"
            >
          </div>
          <!--  -->
          <div
            class="upload"
            @click="sendNDB"
          >
            
          </div>
          <!--  -->
          <div class="upload"
            @click="getOrderingEncryptData"
          >
            
          </div>
        </div>
      </div>

      <div id="origin" v-if="isShowOrigin">
      </div>

      <div class="ndbwraper">
        <div id="ndb0" class="ndb" v-if="isShowEncrypt"></div>
      </div>
      <div class="ndbwraper">
        <div id="ndb1" class="ndb" v-if="isShowEncrypt"></div>
      </div>

      <div id='order' class="encryptdata" v-if="isShowOrder">
      </div>
    </div>
  </div>
</template>

<script>
import * as faceapi from 'face-api.js'
import { getModels } from '../../util/getface.js'
import { MyNDB } from '../../util/getNDB.js'
import axios from 'axios'

const encrypt = require('ordering-encrypt')

export default {
  name: 'FaceRecognize',
  data () {
    return {
      num: 0,
      userId: '',
      originData: [],
      encryptData: '',
      Pos: [],
      orderingEncryptData: '',
      imgUrl: '',
      hasImg: true,
      descriptor: [],
      before_s: '',
      isShowOrigin: false,
      isShowEncrypt: false,
      isShowOrder: false,
      data: {
        userID: undefined,
        NDB: '',
        flag: [],
        specific: ''
      }
    }
  },
  methods: {
    onImgchange: async function (e) {
      // 
      const image = await faceapi.bufferToImage(e.target.files[0])

      // 
      var flag
      if (this.hasImg === true) {
        flag = true
        this.hasImg = false
      } else {
        flag = false
      }

      // img
      const img = document.querySelector('#Img')
      img.setAttribute('src', image.src)

      // 
      const canvas = faceapi.createCanvasFromMedia(img)
      const displaySize = {
        width: img.width,
        height: img.height
      }
      // canvas.setAttribute('class', 'mycanvas')
      canvas.style = `position:absolute; left:50%; top:0; transform: translate(-50%)`
      const photodiv = document.querySelector('#photo .container')
      // console.log(photodiv)
      if (flag === false) {
        document.querySelector('#photo .container').removeChild(document.querySelector('#photo .container').children[1])
      }
      photodiv.append(canvas)
      // 
      faceapi.matchDimensions(canvas, displaySize)

      // 
      const detections = await faceapi.detectAllFaces(image).withFaceLandmarks().withFaceDescriptors()

      this.descriptor = []

      // 
      const resizedDetections = faceapi.resizeResults(detections, displaySize)
      resizedDetections.forEach(detection => {
        const box = detection.detection.box
        const drawBox = new faceapi.draw.DrawBox(box, {
          label: 'face'
        })
        drawBox.draw(canvas)
        faceapi.draw.drawDetections(canvas, resizedDetections)
        faceapi.draw.drawFaceLandmarks(canvas, resizedDetections)
        this.descriptor.push(detection.descriptor)
      })
      console.log(this.descriptor[0])
      this.originData = this.descriptor[0]
      this.isShowOrigin = true
      setTimeout(this.drawOriginData, 0)
    },
    sendNDB: function () {
      if (this.descriptor[0] === undefined) {
        alert('')
      } else {
        let ndb = new MyNDB(this.descriptor[0])
        this.Pos = ndb.Pos
        this.encryptData = ndb.NDB
        this.isShowEncrypt = true
        this.data.NDB = ndb.NDB
        this.data.flag = ndb.flag
        this.data.specific = ndb.specific
        this.data.userID = this.userId
        setTimeout(this.drawNdbData, 0)
        this.postNDB()
      }
    },
    getOrderingEncryptData: function () {
      if (this.descriptor[0] === undefined) {
        alert('')
      } else {
        let ndb = new MyNDB(this.descriptor[0])
        // console.log(ndb.before_s);
        let data = String(ndb.before_s).split('').map(a => { return Number(a) })
        console.log(data)
        let p = new Array(data.length).fill(0)
        let final = encrypt.decode(data, p)
        // console.log('encrypt data:', final.join(''), 'keydata:', p.join(''))
        // finalp
        // let saveData = {
        //   username: '12312313',
        //   part: final.join(''),
        //   p: p.join('')
        // }
        this.orderingEncryptData = final
        this.isShowOrder = true
        setTimeout(this.drawOrderChart, 0)
        this.postOrder()
      }
    },
    postOrder: function () {
      axios({
        method: 'post',
        url: 'http://127.0.0.1:8000/faceRecognize_part/',
        dataType: 'json',
        data: {
          'userID': this.$store.state.userID,
          'part': JSON.stringify(this.orderingEncryptData)
        }
      }).then((response) => {
        console.log(response)
        if (response.data.result === true) {
          alert('')
        } else {
          alert('')
        }
      })
    },
    drawOrderChart: function () {
      // console.log('drawOrderChart>query#order', document.querySelector('#order'))
      let orderCharts = this.$echarts.init(document.querySelector('#order'))
      let xAxisData = []
      for (let i = 0; i < this.orderingEncryptData.length; i++) {
        xAxisData.push(i)
      }
      // 
      orderCharts.setOption(
        {
          title: { text: '' },
          xAxis: {
            type: 'category',
            data: xAxisData
          },
          yAxis: {
            type: 'value'
          },
          series: [{
            data: this.orderingEncryptData,
            type: 'line',
            smooth: true
          }]
        }
      )
    },
    postNDB: function () {
      axios({
        method: 'post',
        url: 'http://127.0.0.1:8000/faceRecognize_native/',
        dataType: 'json',
        data: JSON.stringify(this.data)
      }).then((response) => {
        console.log(response)
        if (response.data.result === true) {
          alert('')
        } else {
          alert('')
        }
      })
    },
    getOriginData: function () {
      var file = document.querySelector('input[type=file]').files[0]
      if (!file) {
        this.originData = '!'
      } else {
        this.originData = this.descriptor[0]
      }
      this.isShowOrigin = !this.isShowOrigin
    },
    getEncryptData: function () {
      var file = document.querySelector('input[type=file]').files[0]
      if (!file) {
        this.encryptData = '!'
      } else {
        if (this.encryptData === '') {
          this.encryptData = '!'
        }
      }
      this.isShowEncrypt = !this.isShowEncrypt
    },
    drawOriginData: function () {
      // domecharts
      let myChart = this.$echarts.init(document.querySelector('#origin'))
      let data = []
      for (let i = 0; i < 128; i++) {
        data[i] = i + 1
      }
      let originData = []
      for (let i = 0; i < 128; i++) {
        originData.push(this.originData[i])
      }
      // 
      myChart.setOption({
        title: { text: '' },
        xAxis: {
          data: data
        },
        yAxis: {},
        series: [{
          name: '',
          type: 'bar',
          data: originData
        }]
      })
    },
    drawNdbData: function () {
      let ndbChart0 = this.$echarts.init(document.querySelector('#ndb0'))
      let ndbChart1 = this.$echarts.init(document.querySelector('#ndb1'))
      let data = []
      for (let i = 0; i < 1280; i++) {
        data[i] = i + 1
      }
      console.log(this.Pos)
      let data0 = []
      for (let i = 0; i < 1280; i++) {
        data0[i] = this.Pos[i][0]
      }
      let data1 = []
      for (let i = 0; i < 1280; i++) {
        data1[i] = this.Pos[i][1]
      }
      console.log(data1)
      let option0 = {
        title: { text: '' },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        legend: {
          data: ['0']
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [
          {
            type: 'category',
            data: data
          }
        ],
        yAxis: [
          {
            type: 'value'
          }
        ],
        series: [
          {
            name: '0',
            type: 'bar',
            emphasis: {
              focus: 'series'
            },
            data: data0
          }
        ]
      }
      let option1 = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        legend: {
          data: ['1']
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [
          {
            type: 'category',
            data: data
          }
        ],
        yAxis: [
          {
            type: 'value'
          }
        ],
        series: [
          {
            name: '1',
            type: 'bar',
            emphasis: {
              focus: 'series'
            },
            data: data1
          }
        ]
      }
      ndbChart0.setOption(option0)
      ndbChart1.setOption(option1)
    }
  },
  mounted () {
    getModels()
    this.userId = localStorage.getItem('NDB_userID')
  }
}
</script>

<style lang='scss' scoped>
.line {
  border-bottom: 1px solid white;
}
.facerecognize {
  color: black;
  .user-info {
    background: url('https://static3.sycdn.imooc.com/static/img/u/temp1.png') no-repeat center top #000;
    background-size: cover;
    height: 100px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    .user-info-wraper {
      width: 60%;
      position: relative;
      height: 100%;
      .user-pic-wraper {
        position: absolute;
        width: 100px;
        height: 100px;
        top: 20%;
        border-radius: 50%;
        border: 2px solid white;
        box-shadow: 0 4px 8px 0 rgb(7, 17, 27);
        background: white;
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center;
        .user-pic {
          text-align: center;
          height: 100px;
        }
      }
    }
    .usermsg-userid {
      color: white;
      font-size: 20px;
      font-weight: 600;
      position: absolute;
      left: 130px;
      top: 30%;
    }
  }
  .content {
    width: 60%;
    margin: 50px auto;
    padding-bottom: 50px;
    .photobox {
      border-radius: 4px;
      box-shadow: 0 8px 14px 0 rgb(42, 44, 46);
      #photo {
        position: relative;
        text-align: center;
        .photo-title{
          padding: 20px 0 10px 0;
          font-size: 20px;
          font-weight: 900;
          line-height: 60px;
        }
        .container {
          height: 400px;
          overflow: auto;
          display: block;
          margin: 0 auto;
          position: relative;
          background: rgb(191, 197, 201);
          #Img {
            height: 100%;
          }
        }
      }
      .inputwrap{
        display: flex;
        justify-content: center;
        align-items: center;
        .upload{
          padding: 4px 10px;
          margin: 10px 45px;
          height: 20px;
          line-height: 20px;
          position: relative;
          cursor: pointer;
          color: #888;
          background: #fafafa;
          border: 1px solid #ddd;
          border-radius: 4px;
          overflow: hidden;
          display: inline-block;
          *display: inline;
          *zoom: 1;
          width: auto;
          #imageUpload {
            position: absolute;
            font-size: 100px;
            right: 0;
            top: 0;
            opacity: 1;
            filter: alpha(opacity=0);
            cursor: pointer
          }
        }
        .upload:hover{
          color: rgb(200, 22, 35);
          background: #eee;
          border-color: #ccc;
          text-decoration: none;
          cursor: pointer;
        }
      }
    }
    #origin {
      margin: 50px 0;
      height: 500px;
    }
    .ndbwraper {
      overflow: auto;
      width: 100%;
      .ndb {
        margin: 50px 0;
        height: 500px;
        width: 100%;
      }
    }
  }
}

#order{
  overflow: auto;
  height: 500px;
}

.encryptdata {
  height: 350px;
}

.encryptbtn {
  padding: 10px;
}

.encryptdata {
  height: 350px;
  overflow: auto;
  font-family: monospace;
  margin-top: 10px;
  padding-left: 10px;
  .errorencrypt {
    font-size: 20px;
    font-weight: 800;
    text-align: center;
    line-height: 350px;
  }
}
.data-desc{
  word-break: break-word;
}
</style>



