<template>
  <div class="header">
    <div class="content">
      <div class="title"></div>
      <div class="info"></div>
      <div class="news">,,</div>
      <div class="btn">
        <div class="btn-primary" @click="ready"></div>
        <a href="https://github.com/wk-Nemo/MyFace" class="btn-document"></a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Header',
  methods: {
    ready: function () {
      this.$router.push('/facephoto')
    }
  }
}
</script>

<style lang="scss" scoped>
.header {
  background-image: url("https://ai.bdstatic.com/file/2DB693AACDDE49C0940410B3FB74DFA0") !important;
  height: 300px;
  background: no-repeat 50%;
  background-size: cover;
  overflow: hidden;
  position: relative;
  .content {
    overflow: hidden;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 400px;
    transform: translate(-50%,-50%);
    padding-right: 450px;
    .title {
      font-size: 40px;
      line-height: 90px;;
    }
    .info {
      font-size: 8px;
      line-height: 15px;
      margin: 10px 0;
    }
    .news {
      font-size: 8px;
      color: #faad15;
      line-height: 15px;
      margin: 10px 0;
    }
    .btn {
      display: flex;
      font-size: 8px;
      margin-top: 26px;
      .btn-primary {
        background-color: rgb(3,111,226);
        padding: 7px 15px;
        margin-right: 40px;
        font-weight: 100;
      }
      .btn-primary:hover {
        cursor: pointer;
        background-color: rgb(28,136,252);
      }
      .btn-document {
        background-color: #ccc;
        padding: 7px 15px;
        font-weight: 100;
      }
      .btn-document:hover {
        background: white;
        color:black;
        cursor: pointer;
      }
    }
  }
}
</style>



