          url: 'http://www.xinhuanet.com/techpro/2021-01/26/c_1127024502.htm',
          photo: 'http://n.sinaimg.cn/tech/crawl/59/w550h309/20201127/2e8b-kentcvx8652740.jpg',
          title: ' ',
          content: ''
        }
      ],
      isActiveIndex: 0,
      activeList: 0
    }
  },
  methods: {
    changeStyle: function (index) {
      this.isActiveIndex = index
    },
    handleTabFix () {
      var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      // var sectionTop = document.querySelector('#section').offsetTop
      var newsTop = document.querySelector('#news').offsetTop
      var superiorityTop = document.querySelector('#superiority').offsetTop
      scrollTop > 340 ? this.isfixTab = true : this.isfixTab = false
      if (scrollTop < newsTop) {
        this.activeList = 0
      } else if (scrollTop > newsTop && scrollTop < superiorityTop) {
        this.activeList = 1
      } else {
        this.activeList = 2
      }
    }
  },
  mounted () {
    // document.querySelector('.home').addEventListener('scroll', this.handleTabFix, true)
    window.addEventListener('scroll', this.handleTabFix, true)
  }
}
</script>

<style lang="scss" scoped>
.fixedNavbar {
  position: fixed !important;
  left: 0;
  right: 0;
  top: 0;
  background: white;
  z-index: 10;
}
.section {
  padding-top: 80px;
  margin: 0 auto;
  color: black;
  width: 75%;
  .section-title {
    font-size: 20px;
    text-align: center;
  }
  .section-container {
    margin-top: 20px;
    .section-list {
      display: flex;
      margin: 20px 0;
      .section-item {
        flex: auto;
        height: 250px;
        margin: 40px 20px;
        width: 400px;
        transition: transform .5s ease;
        .section-item-img {
          width: 100%;
        }
        .section-item-info {
          box-shadow: 0 5px 20px 0 rgb(210, 208, 208);
          border: 1px solid #eee;
          padding: 20px 48px 22px;
          height: 120px;
          .section-item-info-title {
            font-size: 15px;
            margin-bottom: 20px;
          }
          .section-item-info-content{
            color: #999;
            font-size: 8px;
          }
        }
      }
      .section-item:hover {
        cursor: pointer;
        transform: scale(1.10);
      }
    }
  }
}
.news {
  padding-top: 180px;
  margin: 0 auto;
  color: black;
  width: 75%;
  .news-title {
    font-size: 20px;
    text-align: center;
  }
  .news-container {
    margin-top: 20px;
    display: flex;
    .news-nav {
      width: 200px;
      background: rgb(247, 247, 247);
      padding: 20px 0;
      .news-nav-item {
        padding: 0 10px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        height: 20px;
        font-size: 8px;
        line-height: 20px;
        color: #666;
      }
      .news-nav-item-active {
        height: 35px;
        line-height: 35px;
        background: rgb(11,116,233);
        color: white;
      }
      .news-nav-item:hover {
        color:  rgb(11,116,233);
        cursor: pointer;
      }
      .news-nav-item-active:hover {
        color: white;
      }
    }
    .news-content {
      flex: 1;
      padding: 20px;
      display: flex;
      .news-content-left{
        padding-right: 20px;
        flex: 1;
        .news-content-title{
          font-size: 16px;
          font-weight: 600;
        }
        .content {
          margin: 20px 0;
          font-size: 6px;
          line-height: 17px;
        }
      }
      .news-content-right {
        width: 250px;
        img {
          width: 100%;
        }
      }
    }
  }
}
.superiority {
  padding-top: 80px;
  margin: 0 auto;
  color: black;
  width: 75%;
  .superiority-title {
    font-size: 20px;
    text-align: center;
  }
  .superiority-container {
    margin: 20px 0;
    text-align: center;
    .encrypt {
      display: flex;
      height: 500px;
      margin: 20px 0;
      .ndb {
        flex: 1;
        position: relative;
        width: 480px;
        .ndb-title {
          display: flex;
          position: absolute;
          left: 50%;
          transform: translate(-50%);
          .ndb-title-img {
            width: 30px;
            height: 30px;
            background-image: url("https://ai.bdstatic.com/file/D0E2AF8227C24812A26B9AC1FDE8D647");
            background-size: 30px 30px;
          }
          .ndb-title-msg {
            font-size: 18px;
            font-weight: 900;
            line-height: 30px;
            margin-left: 20px;
          }
        }
        .ndb-content {
          padding: 10px;
          margin-top: 50px;
          font-size: 6px;
          line-height: 17px;
          text-align: left;
        }
      }
      .line {
        border-left: 1px solid rgb(247,247,247);
      }
      .part {
        flex: 1;
        position: relative;
        width: 480px;
        .part-title {
