            height: 30px;
            background-size: 30px 30px;
          }
          .part-title-msg {
            font-size: 18px;
            font-weight: 900;
            line-height: 30px;
            margin-left: 20px;
          }
        }
        .part-content {
          padding: 10px;
          margin-top: 50px;
          font-size: 6px;
          line-height: 17px;
          text-align: left;
        }
      }
    }
  }
}
</style>



