/* eslint-disable */
class MyNDB {
  constructor (descriptor) {
    this.originalData = this.getOriginal(descriptor) 
    this.before_s = this.originalData.before_s // s
    this.lens = this.originalData.lens         // s128
    this.flag = this.originalData.flag         // 
    this.m = this.before_s.length              // s
    this.r = 10
    this.cn = this.m * this.r                  // NDB
    this.specific = this.getSpecific()         // 
    this.s = this.xor(this.before_s)           // s
    this.p = [0, 0.95, 0.03, 0.02]             // p
    this.q = [0.55, 0.05, 0.05, 0.05, 0.05, 0.05, 0.05, 0.05, 0.05, 0.05] // q
    this.posbility = this.getPosibility()      // [i][0]Pdiff[i][1]Psame
    this.cc = this.getCC()                     // 0-102301
    this.NDB = this.f(this.s)                  // 
    this.Pos = this.num_01()                   // 
    this.Gen = this.getAttr()                  // 
    this.trueGen = this.getOriginalGen()       // 
  }

  // 
  OriginalData (before_s, lens, flag) {
    this.before_s = before_s
    this.lens = lens
    this.flag = flag
  }

  // 
  getOriginal (descriptor) {
    let before_s = ''
    let lens = []
    let flag = []
    // s
    for (let i=0; i<128; i++) {
      var n = this.floatTo2(descriptor[i])
      lens[i] = n.len
      before_s = before_s.concat(n.s)
      flag[i] = n.flag
    }
    return new this.OriginalData(before_s, lens, flag)
  }

  // 
  MyNumber(s, len, flag) {
    this.s = s       // 
    this.len = len   // 
    this.flag = flag // 
  }

  // 128
  floatTo2(num) {
    // 
    var a = []
    if(num > 0) {
        var flag = 1
    } else {
        var flag = -1
        num = -num
    }

    // 
    var b = num*Math.pow(10, 3)
    b = Math.round(b)
    b = b.toString(2)
    
    // 0
    for (let i=0; i<10-b.length; i++) {
        a.push('0')
    }

    //
    var front = a.join('')
    var result = front.concat(b)
    
    // MyNumber
    var myNum = new this.MyNumber(result, result.length, flag)
    return myNum
  }

  // 
  getSpecific() {
    var specific_res = []
    for (let i=0; i<1280; i++) {
        let x = Math.random()
        if (x < 0.5) {
            specific_res.push('0')
        } else {
            specific_res.push('1')
        }
    }
    var res = specific_res.join('')
    return res
  }

  // 
  xor(s) {
    let res = []
    for (let i=0; i<1280; i++) {
        if (s[i] === this.specific[i]) {
            res.push('0')
        } else {
            res.push('1')
        }
    }
    let result = res.join('')
    return result
  }

  // posibility
  getPosibility () {
    let posbility = []
    for (let i = 0; i < 10; i++) {   
      posbility[i] = []
      posbility[i][0] = this.diff(i)   //  
      posbility[i][1] = 1 - posbility[i][0]   //
    } 
    return posbility
  }

  // posility
  diff (i) {
    var Ndiff = 0
    var Nsame = 0
    for (let j = 1; j <= 3; j++) {
      Ndiff += j * this.p[j] * this.q[i]    //  6
    }
    for (let j = 1; j <= 3; j++) {
      Nsame += ((3 - j) * this.p[j]) / 10  //10   //7
    }

    var Pdiff = 0
    Pdiff = Ndiff / (Ndiff + Nsame)  //is   5
    return Pdiff
  }

  myTo2 (num) {
    // 
    var a = []
    // 
    var b = num.toString(2)   
    // 0
    for (let i  =0; i < 10 - b.length; i++) {
        a.push('0')
    }
    //
    var front = a.join('')
    var result = front.concat(b) 
    return result
  }

  getCC () {
    let cc = []
    for (let i = 0; i <= 1023; i++) {
      cc.push(this.myTo2(i))
    }
    return cc
  }

  // 
  generateRandomNumbers(l) { 
    var count = 0
    for (let i=9; i>=0; i--) {
      count += this.q[i]
      if (l < count) {
        return i
      }
    }
  }

  //01
  judge(s) {
    if(s === '1') {
      return '0'
    } else {
      return '1'
    }
  }

  // 
  Ent(p, c) {
    this.p = p  // 
    this.c = c  //'0''1
  }

  //
  f (s) {
    let NDB = []
    var n = 0
    do {
      var v_p =[]
      var v_c = []
      var v = new this.Ent(v_p, v_c)
      //0-1
      var t = Math.random()
      //
      if(t < this.p[1]) {
        // console.log('1')
        var diff1 = 0
        var same1 = 0
        var same2 = 0
        var attr = 0
        // 
        attr = Math.floor(Math.random()*128)

        // q  
			  diff1 = this.generateRandomNumbers(Math.random())
        // 
			  v.p[0] = diff1 + attr * 10 
        // s
			  v.c[0] = this.judge(s[v.p[0]]) 

        same1 = Math.floor(Math.random()*10)
        while (same1 === diff1) {   //
          same1 = Math.floor(Math.random()*10)
        }
        v.p[1] = same1 + attr * 10;    //
        v.c[1] = s[v.p[1]];

        same2 = Math.floor(Math.random()*10)
        while (same2 === diff1 || same2 === same1) {
          same2 = Math.floor(Math.random()*10)
        }
        v.p[2] = same2 + attr * 10;
        v.c[2] = s[v.p[2]];
      }
      //
      else if(t < this.p[1] + this.p[2]) {
        var diff1 = 0
        var diff2 = 0
        var same1 = 0
        var attr = 0
  			attr = Math.floor(Math.random()*128)
        // q  
  			diff1 = this.generateRandomNumbers(Math.random())
        // 
	  		v.p[0] = diff1 + attr * 10;    
        // s
		  	v.c[0] = this.judge(s[v.p[0]])  
        diff2 = this.generateRandomNumbers(Math.random())
        while (diff2 === diff1) {
  				diff2 = this.generateRandomNumbers(Math.random());
	  		}
        v.p[1] = diff2 + attr * 10;
		  	v.c[1] = this.judge(s[v.p[1]]) 

        same1 = Math.floor(Math.random()*10)
			  while (same1 === diff1 || same1 === diff2) {
				  same1 = Math.floor(Math.random()*10)
			  }
			  v.p[2] = same1 + attr * 10;
			  v.c[2] = s[v.p[2]];
      }
      //
      else {
        var diff1 = 0
        var diff2 = 0
        var diff3 = 0
        var attr = 0
        // 
			  attr = Math.floor(Math.random()*128)
        // q  
			  diff1 = this.generateRandomNumbers(Math.random())
        // 
			  v.p[0] = diff1 + attr * 10    
        // s
			  v.c[0] = this.judge(s[v.p[0]])   
        diff2 = this.generateRandomNumbers(Math.random())
        while (diff2 === diff1) {
			  	diff2 = this.generateRandomNumbers(Math.random());
			  }
        v.p[1] = diff2 + attr * 10
		  	v.c[1] = this.judge(s[v.p[1]]) 

        diff3 = this.generateRandomNumbers(Math.random())
        while (diff3 === diff1 || diff3 === diff2) {
				  diff3 = this.generateRandomNumbers(Math.random());
			  }
        v.p[2] = diff3 + attr * 10;
			  v.c[2] = this.judge(s[v.p[2]]) 
      }
      // 
      NDB[n] = v
      n++
    }while(n < this.cn)
    return NDB
  }

  /**/ 
  // 01
  num_01 () {
    let Pos = []
    for (let i = 0; i < this.m; i++) {
      Pos[i] = []
      Pos[i][0] = 0
      Pos[i][1] = 0
	  }
    for (let i = 0; i < this.cn; i++) {
      for (let j = 0; j < 3; j++) {
        if (this.NDB[i].c[j] === '0') {
          Pos[this.NDB[i].p[j]][0]++
        }
        else {
          Pos[this.NDB[i].p[j]][1]++
        }
      }
    }
    return Pos
  }

  /**/
  //i(i    num01)
  Pr (index, num) {
    var pr1 = 0
    var pr0 = 0
    var tmp = index % 10
    var pdiff = this.posbility[tmp][0]
    var psame = this.posbility[tmp][1]
    pr0 = (Math.pow(pdiff, this.Pos[index][1])*Math.pow(psame, this.Pos[index][0])) / (Math.pow(pdiff, this.Pos[index][1])*Math.pow(psame, this.Pos[index][0]) + Math.pow(pdiff, this.Pos[index][0])*Math.pow(psame, this.Pos[index][1]))
    if (num === '1') {
      pr1 = 1 - pr0
      return pr1
    }
    else {
      return pr0
    }
  }

  // -999-999
  // index0 11 22 ...128
  // real-999-999
  calculate(index, real) {
    // sum
    var sum = 1
    // sum
    for (let i=0; i<10; i++) {
      // indexcc-999999
      var tmp = this.Pr(index + i, this.cc[real][i])
      sum = sum*tmp
    }
    // 3
    // sum *= Math.pow(real, 2)
    sum *= real
    return sum
  }

  // 
  // index0 10 20 ... 1270128
  E (index) {
    // tmp
    var tmp = 0
    for (let i=0; i<=1023; i++) {
      tmp += this.calculate(index, i) 
    }
    // tmp = Math.pow(tmp, 0.5)
    return tmp
  }

  // 128
  getAttr () {
    var count = 0
    let Gen = []
    for (let i=0; i<this.m; i++) {
      if (i % 10 === 0) {
        Gen[count] = this.E(i) * 0.001
        count++
      }
    }
    return Gen
  }

  // s128
  to128Float (s, lens) {
    var res = []
    var count = 0
    // slice128
    for (let i=0; i<128; i++) {
      res[i] = s.slice(count, count+lens[i])
      count = count + lens[i] 
    }
    return res
  }

  // 
  // 128
  toFloat(s, flag) {
    var num = 0
    var count = 9
    for (let i=0; i<10; i++) {
      if (s[i] === '1') {
        num += Math.pow(2, count)
      }
      count--
    }
    num = num*flag
    return num
  }

  getOriginalGen () {
    // 
    // Gens
    var after_s = ''
    for (let i=0; i<128; i++) {
      var n = this.floatTo2(this.Gen[i])
      after_s = after_s.concat(n.s)
    }
    // s
    var result_s = this.xor(after_s)
    var result_128s = this.to128Float(result_s, this.lens)
    var result_nums = []
    for (let i=0; i<128; i++) {
      var result_num = this.toFloat(result_128s[i], this.flag[i])
      result_nums.push(result_num)
    }
    return result_nums
  }
}

export  { MyNDB }


